.insta-parser-container {
    margin: auto;
    max-width: 935px; }

.insta-parser img{
    width: 100%;
}
.insta-parser {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin: 0 -8px 16px; }
.insta-parser .instagram-photo {
    border: 4px solid #fff;
    -webkit-box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.1);
    box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.1);
    -webkit-box-flex: 1;
    -webkit-flex: 1 0 auto;
    -ms-flex: 1 0 auto;
    flex: 1 0 auto;
    margin: 8px;
    position: relative;
    width: -webkit-calc(100% / 2 - (8px * 2));
    width: calc(100% / 2 - (8px * 2));
    max-width: 293px; }

@media only screen and (min-width: 768px) {
    .insta-parser .instagram-photo {
        width: -webkit-calc(100% / 4 - (8px * 2));
        width: calc(100% / 4 - (8px * 2)); } }

.insta-parser .instagram-photo:focus::after, .insta-parser .instagram-photo:hover::after {
    background: rgba(0, 0, 0, 0.5);
    bottom: 0;
    content: "";
    display: block;
    left: 0;
    position: absolute;
    right: 0;
    z-index: 1;
    top: 0; }

.insta-parser .post_icons {
    display: none;
    cursor: pointer;
    z-index: 2;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    position: absolute; }
.insta-parser .post_icons ul {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex; }
.insta-parser .post_icons ul li {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex; }
.insta-parser .post_icons ul li .inst-icon {
    background-image: url("../images/insta.png"); }
.insta-parser .post_icons ul li .inst-count {
    color: white;
    margin-left: 5px;
    margin-right: 20px; }
.insta-parser .post_icons ul li .icon-like {
    background-repeat: no-repeat;
    background-position: 0px 0px;
    height: 19px;
    width: 19px; }
.insta-parser .post_icons ul li .icon-comment {
    background-repeat: no-repeat;
    background-position: -42px 0;
    height: 19px;
    width: 19px; }

.insta-parser .instagram-photo:focus .post_icons, .insta-parser .instagram-photo:hover .post_icons {
    display: block; }